.form-button{
    margin-top: 1rem; 
}

.inline{
    display: inline-block;
}

.output{
    -webkit-animation: 1.0s ease-out 0s 1 onUpdate;
            animation: 1.0s ease-out 0s 1 onUpdate; 
    -webkit-transition: 0.2ms all ease-out; 
    transition: 0.2ms all ease-out;
}

@-webkit-keyframes onUpdate{
    0%{
        background-color: gray;
    }
    100%{
        background-color: white; 
    }

}

@keyframes onUpdate{
    0%{
        background-color: gray;
    }
    100%{
        background-color: white; 
    }

}
